import React from "react";
import ReactApexChart from "react-apexcharts";

class ApexChart extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      series: [
        {
          data: [21, 22, 10, 28, 16, 21, 13, 30, 40, 23, 25, 12],
        },
      ],
      options: {
        tooltip: {
          enabled: true,
          theme: "dark",
          fixed: {
            position: "topLeft",
          },
          x: {
            show: false,
          },
          y: {
            formatter: (value) => `$${value * 100}`,
          },
          onDatasetHover: {
            highlightDataSeries: true,
          },
          fillSeriesColor: true,
          followCursor: false,
          style: {
            color: "1E90FF",
          },
        },
        grid: { show: false },
        fill: { colors: ["#1E90FF"] },
        yaxis: {
          labels: {
            formatter: (val) => {
              return val + "k";
            },
          },
        },
        chart: {
          height: 350,
          type: "bar",
          events: {
            click: function (chart, w, e) {
              // console.log(chart, w, e)
            },
          },
          selection: {
            fill: {
              color: "purple",
            },
          },
        },
        plotOptions: {
          bar: {
            columnWidth: "25%",
            distributed: true,
            borderRadius: 5,
          },
        },
        dataLabels: {
          enabled: false,
        },
        legend: {
          show: false,
        },
        xaxis: {
          categories: [
            ["Jan"],
            ["Feb"],
            ["Mar"],
            "Apr",
            ["May"],
            ["Jun"],
            ["Jul"],
            ["Aug"],
            ["Sep"],
            ["Oct"],
            ["Nov"],
            ["Dec"],
          ],
          labels: {
            style: {
              fontSize: "12px",
              textAlign: "center",
            },
          },
        },
      },
    };
  }

  render() {
    return (
      <div id="chart" className="chart">
        <ReactApexChart
          options={this.state.options}
          series={this.state.series}
          type="bar"
          height={200}
        />
      </div>
    );
  }
}

export default ApexChart;
